.product {
  margin-left: 20px;
  margin-right: 20px;
  margin-bottom: 20px;
  display: inline-block;
  width: 320px;
  height: 520px;
}

@media (min-width: 1080px) {
  .product {
    margin: 10px 20px
  }
}
@media (min-width: 1200px) {
  .product {
    margin: 0px 40px;
  }
}


/******Card*******/

.card {
  position: relative;
  z-index: 0;
  margin-top: 43px;
  display: inline-block;
  width: 320px;
  height: 437px;
  font-size: 0;
  cursor: pointer;
  background-color: #1698d9;
  border-bottom-right-radius: 12px;
  border-bottom-left-radius: 12px;
}
.card:before,
.card:after{
  position: absolute;
  top: -43px;
  display: inline-block;
  width: 160px;
  height: 43px;
  content: '';
  background-color: #1698d9;
}
.card:before {
  left: 21px;
  transform: skew(-45deg);
}
.card:after {
  right: 0;
  border-top-right-radius: 12px;
}
.card__bg,
.card__inner {
  display: inline-block;
  width: 312px;
  height: 433px;
  border-bottom-right-radius: 8px;
  border-bottom-left-radius: 8px;
}
.card__bg {
  position: relative;
  z-index: 1;
  background-color: #f2f2f2;
}
.card__bg:before,
.card__bg:after {
  position: absolute;
  top: -39px;
  display: inline-block;
  width: 156px;
  height: 39px;
  content: '';
  background-color: inherit;
}
.card__bg:before {
  left: 20px;
  transform: skew(-45deg);
}
.card__bg:after {
  z-index: -1;
  right: 0;
  border-top-right-radius: 8px;
}


/****Card__active*****/
.card__active {
  position: relative;
  z-index: 0;
  margin-top: 43px;
  display: inline-block;
  width: 320px;
  height: 437px;
  font-size: 0;
  cursor: pointer;
  background-color: #e62e7a;
  border-bottom-right-radius: 12px;
  border-bottom-left-radius: 12px;
}
.card__active:before,
.card__active:after {
  position: absolute;
  top: -43px;
  display: inline-block;
  width: 160px;
  height: 43px;
  content: '';
  background-color: #e62e7a;
}
.card__active:before {
  left: 21px;
  transform: skew(-45deg);
}
.card__active:after {
  right: 0;
  border-top-right-radius: 12px;
}


.card__inner {
  z-index: 2;
  padding-left: 40px;
  font-family: 'Trebuchet MS', sans-serif;
  text-align: left;
  background-image: url('Photo.svg');
  background-repeat: no-repeat;
  background-position: bottom;
}

.card__label {
  position: absolute;
  top: -20px;
  margin-left: 2px;
  font-size: 16px;
  color: #666;
}

.card__title,
.card__descr,
.card__specs {
  display: block;
  float: left;
}

.card__title {
  margin-top: 7px;
  font-size: 48px;
  font-weight: bold;
  color: #000;
}
.card__descr {
  margin-top: 3px;
  margin-left: 2px;
  font-size: 24px;
  font-weight: bold;
  color: #000;
}
.card__specs {
  margin-top: 19px;
  margin-left: 3px;
  width: 100%;
  font-size: 14px;
  line-height: 15px;
  color: #666;
}


/*****Card__disabled*******/
.card__disabled {
  background-color: #b3b3b3;
  position: relative;
  z-index: 0;
  margin-top: 43px;
  display: inline-block;
  width: 320px;
  height: 437px;
  font-size: 0;
  cursor: default;
  border-bottom-right-radius: 12px;
  border-bottom-left-radius: 12px;
}
.card__disabled:before,
.card__disabled:after {
  position: absolute;
  top: -43px;
  display: inline-block;
  width: 160px;
  height: 43px;
  content: '';
  background-color: #b3b3b3;
}
.card__disabled:before {
  left: 21px;
  transform: skew(-45deg);
}
.card__disabled:after {
  right: 0;
  border-top-right-radius: 12px;
}
.card__inner__disabled {
  display: inline-block;
  width: 312px;
  height: 433px;
  border-bottom-right-radius: 8px;
  border-bottom-left-radius: 8px;
  opacity: 0.5;
  z-index: 2;
  padding-left: 40px;
  font-family: 'Trebuchet MS', sans-serif;
  text-align: left;
  background-image: url('Photo.svg');
  background-repeat: no-repeat;
  background-position: bottom;
}


.weight {
  position: absolute;
  right: 12px;
  bottom: 12px;
  padding-top: 14px;
  width: 80px;
  height: 80px;
  font-family: 'Trebuchet MS', sans-serif;
  font-size: 42px;
  text-align: center;
  color: #fff;
  background-color: #1698d9;
  border-radius: 50%;
}

.weight__active {
  background-color: #e62e7a;
  position:absolute;
  right: 12px;
  bottom: 12px;
  padding-top: 14px;
  width: 80px;
  height: 80px;
  font-family: 'Trebuchet MS', sans-serif;
  font-size: 42px;
  text-align: center;
  color: #fff;
  border-radius: 50%;
}

.weight__count {
  font-size: 42px;
  line-height: 37px;
}

.weight__measure {
  font-size: 21px;
}

.weight__disabled {
  background-color: #b3b3b3;
  position:absolute;
  right: 12px;
  bottom: 12px;
  padding-top: 14px;
  width: 80px;
  height: 80px;
  font-family: 'Trebuchet MS', sans-serif;
  font-size: 42px;
  text-align: center;
  color: #fff;
  border-radius: 50%;
}